body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.carousel-item img{
 height:500px;
 object-fit: cover;
}
.carousel-item{
height: 500px;
padding: 0 20px;
font-family: 'Mukta', sans-serif;
}
.carousel-control-next-icon {
 filter: invert(1);
  box-shadow: 0px 0px 5px 12px white;
}
.carousel-control-prev-icon {
 filter: invert(1);
  box-shadow: 0px 0px 5px 12px white;
}
/* Navbar CSS */
.navbar{
  
  background: linear-gradient(90deg, #323232 ,black);
}
.navbar ul{
  display: flex;
  gap: 35px;
  justify-content: center;
  align-items: center;
  flex-flow: row;
  margin: 0;
  padding: 15px 55px;
  font-family: 'Mukta', sans-serif;
}
.navbar ul a{
  text-decoration: none;
}
.navbar ul h3{
  color: #cf9519;
}
.navbar ul li{
  list-style:none;
  text-decoration: none;
  color: black;
}
.navbar ul li a{
  text-decoration: none;
  color: #d2940d;
  font-size: larger;
  font-weight: 500;
}

.navbar ul li:hover{
  color:#b47e0a ;
  transform: scale(0.91);
  transition-duration: 0.4s;
}
.Navbar ul{
  display: flex;
  gap: 25px;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  margin: 0;
  font-family: 'Mukta', sans-serif;
}
.welcome{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: rgb(32 52 69);
  
  font-family: fangsong;

}
.welcome h1{
  font-size: 36px;
  text-align: center;
  color: white;
}
.welcome p{
  color: rgb(221, 221, 221);
    font-size: 14px;
    padding: 0px;
    margin: -9px 0 7px 0px;
    text-align: center;
    text-shadow: 1px 1px 15px black;

}

#root{
  font-family: fangsong;
}

.services{
  margin-top: 10px;
  padding: 31px 0;
  margin-bottom: 25px;
}
.OurServices{
  display: flex;
  justify-content: space-between;
  padding: 0 55px;
}
.OurServices h3{

  cursor: pointer;
    border: 2px solid transparent;
    display: flex;
    font-size: 25px;
    font-weight: 700;
    box-shadow: 5px 5px 10px 4px rgb(34 34 34);
    border-radius: 12px;
    padding: 15px;
    background-color: rgb(32 52 69);
    width: 420px;
    color: white;
    justify-content: center;
    
}
.services h1{
  
  font-size:60px;
  font-weight: 600;
  text-align: center;
}

.homeInfo{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 25px 0px 35px 0px;
  background-color: rgb(32 52 69);
  color: White;
  font-family: 'Mukta', sans-serif;
}
.homeInfo h2{
  text-align: center;
}
.homeInfo h1{
  font-weight: 500;
    text-align: left;
    align-self: flex-start;
    padding: 5px 0 0 42px;
    margin-bottom: 20px;
    font-size: 49px;
    color: rgb(235, 235, 235);
}
.footer h1{
  font-weight: 500;
    text-align: left;
    align-self: flex-start;
    padding: 15px 0 0 42px;
    font-size: 49px;
    color: rgb(235, 235, 235);
}

footer{
  background: linear-gradient(90deg, #323232 ,black);
  height: 320px;
}
.contactDetails{
  color: white;
  font-family: 'Mukta', sans-serif;
  margin-top: 20px;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
}
.contactDetails ul li{
  margin-top: 12px;
  font-size: larger;
  font-weight: 600;
  list-style: none;
  font-family: 'Mukta', sans-serif;
  margin-left: 65px;
}
.contactDetails ul{
  vertical-align: middle;
  display: flex;
  flex-flow: column;
}
.contactDetails ul a{
 text-decoration: none;
 color: white;
 margin-top: 15px;
 margin-left: 35px;

}
.contactDetails ul img{
  width: 30px; 
  
  margin-top: -1px;

  margin-right: 12px;
}


/* CATEGORIES.js */

.cards{
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  align-items: center;
  justify-content: center;
}
.card{
  /* height: 20rem; */
}
.card img{
  height: 280px;
}
.welcome video{
  width: 100%;
  height: 555px;
  object-fit: cover;
  opacity: 0.8;
  filter: brightness(0.8);
}
.welcome .welcomeLines{
  position: absolute;
  background: #1212128f;
  bottom: 0;
  left: 0px;
  font-family: 'Mukta';
  padding: 35px;

}
.Navbar {
  height: 100%;
}


@media (max-width:450px) {
  .welcome video{
    width: 100%;
    
  }

  .navbar ul h3 {
    font-size: 25px;
    margin: 0;
  }
  .navbar-toggler {
   padding: 2px !important;
  }
