    display: inline-block;
    width: 1em;
    height: 1em;
  }
  .navbar ul {
    display: flex;
    flex-wrap: wrap;
    gap: 19px;
    margin: 0;
    padding: 15px 0;
    font-family: 'Mukta', sans-serif;
    align-content: flex-start;
    justify-content: flex-start;
  }
  .OurServices {
    display: flex;
    justify-content: center;
    flex-flow: column;
    flex-wrap: wrap;
    align-content: center;
    padding: 7px 25px;
}
